import { getReaderStatusGenerator } from './test/helpers.js'
import { wait } from './utils.js'

// I've tested a few different mocking methods for this but I'd recommend using
// the simplest possible method as it gives us the most flexibility.

const noOp = function_ => function_

// Mocks existing axios interceptor functionality
const mockResponseInterceptors = async (request, response, handlers = []) => {
  // Mock response interceptors specifically for PTW. Fragile but simple.

  // This is a very simplistic mock of the request structure
  if (typeof request === 'string') {
    request = { _options: {
      pathname: new URL(request).pathname,
    } }
  }

  // Mock each interceptor returning if rejected
  for (const { fulfilled, rejected } of handlers) {
    // Error interceptor
    if (!response?.status || response.status < 200 || response.status > 299) {
      return rejected({ response, request })
    }

    await fulfilled(response)
  }

  return response
}

// Mock an HTTP method function. If axios is provided then existing interceptors
// will be mocked on the new method mock. mockWrapper can be used to provide
// spies or other functionality.
const mockMethod = (routes, axios, mockWrapper = noOp) => {
  // If no routes are passed return a mock which will respond with the same
  // response object every time
  if (!Array.isArray(routes)) {
    return mockWrapper(request => mockResponseInterceptors(
      request,
      routes,
      axios?.interceptors.response.handlers,
    ))
  }

  return mockWrapper(async request => {

    // Test each route
    let response = routes.find(
      ([matcher]) => typeof matcher === 'string' ? request === matcher : matcher.test(request),
    // Return response or 404
    )?.[1] || { status: 404 }

    // Allow for functions
    if (typeof response === 'function') {
      response = await response(request)
    }

    // Mock any interceptors
    return mockResponseInterceptors(
      request,
      response,
      axios?.interceptors.response.handlers,
    )
  })
}

// These are simplistic defaults for PTW. Status requests and and a very
// simplistic reader lifecycle will be mocked.
const createDefaultMocks = (axios, mockWrapper) => {
  const mock200 = mockMethod({ status: 200 }, axios, mockWrapper)
  let statusGenerator

  const methods = {}

  methods.create = mockWrapper(() => {
    console.error('Called mocked axios.create. This will not return a new instance, which may result in unexpected behaviors.')
    return axios
  })
  methods.get = mockMethod(
    [
      // Get a generated reader status, reset generator on final status.
      [/readerStatus/i, async () => {
        // Loosely mimics terminal claiming behavior
        statusGenerator = statusGenerator || getReaderStatusGenerator()
        const data = statusGenerator.next().value

        if (data.activationStatus === 'data_stored') {
          statusGenerator = null
        }

        await wait(100)
        return {
          status: 200,
          data,
        }
      }],
    ],
    axios,
    mockWrapper,
  )
  methods.head = mock200
  methods.post = mockMethod(
    [
      [/deactivateReader/i, async () => {
        statusGenerator = null
        await wait(100)
        return { status: 204 }
      }],
      [/./i, async () => {
        statusGenerator = statusGenerator || getReaderStatusGenerator()
        await wait(100)
        return { status: 204 }
      }],
    ],
    axios,
    mockWrapper,
  )
  methods.patch = mock200
  methods.delete = mock200

  return methods
}

// Mock basic axios HTTP methods and any existing response interceptors. Does
// mutate the passed axios instance.
// mockWrapper can be used to provide
// spies or other functionality.
// If methodMocks is not provided then bare bones defaults for PTW will be
// provided.
const mock = (
  axios,
  mockWrapper = noOp,
  methodMocks = createDefaultMocks(axios, mockWrapper),
) => {

  let stored = {}

  // Store original methods so the mock can be undone
  for (const [method, mock] of Object.entries(methodMocks)) {
    stored[method] = axios[method]
    axios[method] = mock
  }

  // Add unmock function to clean up when done
  axios.unmock = () => {
    for (const [method, function_] of Object.entries(stored)) {
      axios[method] = function_
    }
    stored = {}
    axios.unmock = null
    return axios
  }

  return axios
}

export {
  mock,
  mockMethod,
  mockResponseInterceptors,
}
